import React, { Component } from 'react';
import Menu from './components/Menu';
import Page from './components/Page';
import * as menuData from './data/router.json';
import './App.css';


class App extends Component {

    constructor(props) {
        super(props);

        this.state = {
            menuItems: menuData.default,
            activePath: this.props.location.pathname,
            metaData: {},
            content: {}
        }
    }

    componentDidMount() {
        let metaData = this.state.menuItems.find(item => item.url === this.state.activePath);
        console.log('==', metaData);
        if (!metaData) {
            console.log('inside');
            metaData = this.state.menuItems.find(item => item.url === '/page-not-found');
        }

        this.setState({
            metaData,
            content: require(`./data/${metaData.data}`)
        });
    }

    render() {

        const { menuItems, content, metaData } = this.state;
        return (
            <div id="wrapper" className="toggled">
                <header id="sidebar-wrapper">
                    <Menu data={menuItems} />
                </header>
                <div id="page-content-wrapper">
                    <Page data={content} meta={metaData} />
                </div>
            </div>
        );
    }
}

export default App;
