import React, { Component } from 'react';
import './Page.css';

class Page extends Component {

    componentDidUpdate() {
        document.title = `exp-anoop: ${this.props.data.title}`;
    }

    render() {
        return (
            <div class="container-fluid">
                <h1 className="page-title">{this.props.data.title}</h1>
                {(this.props.data.updated && <div className="sub-title">Updated on {this.props.data.updated}</div>)}
                <div dangerouslySetInnerHTML={{ __html: this.props.data.content }}></div>
            </div>
        )
    }
}

export default Page;