import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Menu.css'
import github from './github.png';

class Menu extends Component {

    constructor(props) {
        super(props);

        this.state = {
            items: props.data // menuData.default
        };
    }

    render() {
        const { items } = this.state;
        return (
            <div>
                <ul className="sidebar-nav">
                    <li className="sidebar-brand">
                        <Link to="/"><img src={github} className="Github-logo" alt="Github" />  @exp-anoop</Link>
                    </li>
                    {items.map(item => (
                        (item.showInMenu && <li key={item.url}>
                            <Link to={item.url}>{item.title}</Link>
                        </li>
                        )))}
                </ul>
            </div>

        );
    }
}

export default Menu;